
import './styles/styles.css';
import UI from './UI';

/* este evento funciona apenas carga la pag, trae los datos del backend 
actua como un diddmount */
document.addEventListener('DOMContentLoaded', () => {
    const ui = new UI;
    ui.renderBooks();

});

document.getElementById('book-form').addEventListener('submit', (e) => {
    const title = document.getElementById('title').value;
    const author = document.getElementById('author').value;
    const isbn = document.getElementById('isbn').value;
    const image = document.getElementById('image').files;

    /*
    FormData es propio de js es un formulario virtual donde colocamos los datos obtenidos*/
    const formData = new FormData();
    formData.append('image', image[0]);
    formData.append('title', title);
    formData.append('author', author);
    formData.append('isbn', isbn);
    const ui = new UI;
    ui.addANewBook(formData);
    ui.renderMessage('agregado', 'success', 2000);

    e.preventDefault();



});
document.getElementById('books-cards').addEventListener('click', (e) => {
    if (e.target.classList.contains('delete')) {
        const ui = new UI;
        ui.deleteBook(e.target.getAttribute('_id'))
        ui.renderMessage('eliminado', 'danger', 2000);


    }
    e.preventDefault();
});